.statistics {
    margin: 30px;
    width: 450px;
    background-color: #ffffff;
    border-radius: 5px;
    box-shadow: 2px 2px 5px #000000;
    margin-bottom: 30px;
  }

  h2 {
      text-align: center;
    color: #122236;
    font-size: 16px;
    font-weight: 700;
  }

  .statlist {
    display: flex;
    width: 100%;
  }
  
  .statlist li {
    width: 150px;
    text-align: center;
    padding: 15px 0;
    border: 1px solid #e4e9f0;
    background-color: rgb(241, 175, 237); 
    color: #122236;
  }